footer {
    margin-top: 10%;
    padding: 2%;
    background-color: rgb(34, 33, 33);
    color: white;
    display: flex;
    align-items: center;
}

.leftFooter {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.leftFooter>h4 {
    font-family: "Roboto";
    font-size: 20px;
}

.leftFooter>p {
    text-align: center;
    font-size: 1.2vmax;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.leftFooter>img {
    width: 80%;
    margin: 1vmax;
    cursor: pointer;
}

.midFooter {
    width: 60%;
    text-align: center;
}

.midFooter>h1 {
    font-size: 50px;
    font-family: "Roboto";
    color: #eb4034;
}

.midFooter>p {
    max-width: 60%;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    margin: 1vmax auto;
}

.rightFooter {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightFooter>h4 {
    font-family: "Roboto";
    font-size: 1.4vmax;
    text-decoration: underline;
}

.rightFooter>a {
    text-decoration: none;
    font-size: 1.3vmax;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    color: white;
    transition: all 0.5s;
    margin: 0.5vmax;
}

.rightFooter>a:hover {
    color: #eb4034;
}

@media (max-width:600px) {
    .leftFooter>img {
        width: 150%;
        margin: 1vmax;
        cursor: pointer;
    }

    .midFooter>h1 {
        font-size: 18px;
        font-family: "Roboto";
        color: #eb4034;
    }

    .midFooter>p {
        font-size: 1.5vmax;
        font-family: "Roboto";
    }
}